.about__container {
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3),
    0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    padding: 20px;
    border-radius: 5px;
    background-color: white;
    margin: 0 auto;
    margin-top: 100px;
    width: 100%;
    max-width: 600px;
    font-weight: 300;
    border-radius: 10px;
}

.about__container p:first-child {
    font-size: 20px;
}

.about__container span:last-child {
    display: block;
    margin-top: 10px;
}

.img__container{
    text-align: center;
}

.kookoosand-img {
    border-radius: 50%;
    margin-top: 20px; 
    width: 50%;
    height: 50%;
}